.searchbar {
    width          : 70%;
    max-width      : 300px;
    display        : flex;
    align-items    : center;
    justify-content: center;

    margin       : 20px auto;
    border       : 1px solid #bfbfbf;
    border-radius: 20px;
    padding      : 5px;
}

.searchbar input {
    border: none;
}

.searchbar input:focus {
    outline: none;
}

.searchbar svg {
    transform: translateX(-2px);
}

.search-results {
    overflow-y    : scroll;
    display       : flex;
    align-items   : center;
    flex-direction: column;
}

.searchpage-content-wrapper {
    overflow-y: scroll;
}