.fullscreen-card {
    grid-column: 1 / 2;
    grid-row   : 1 / 4;
    padding    : 10vh 10vw;
    overflow-y : scroll;
}

.fullscreen-card .bottom-bar {
    display         : flex;
    background-color: #ddd;
    justify-content : space-between;
    align-items     : center;
    padding         : 0 20px;
}

.fullscreen-card .bottom-bar svg {
    cursor: pointer;
    margin: 5px;
}