.card {
    width      : 300px;
    height     : 300px;
    perspective: 500px;
    margin     : 10px;
}

@media screen and (min-width: 500px) {
    .card {
        width: 500px;
    }
}

.content {
    width        : 100%;
    height       : 100%;
    border-radius: 10px;
    box-shadow   : 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);

    transition     : transform 1s;
    transform-style: preserve-3d;
}

.card.flipped .content {
    transform : rotateY(180deg);
    transition: transform 0.5s;
}


.front,
.back {
    position           : absolute;
    height             : 100%;
    width              : 100%;
    background         : white;
    text-align         : center;
    border-radius      : 16px;
    backface-visibility: hidden;
}

.back {
    transform      : rotateY(180deg);
    text-align     : left;
    display        : flex;
    flex-direction : column;
    justify-content: space-around;
}

.back * {
    margin: 5px 10px;
}

.front {
    display        : flex;
    align-items    : center;
    justify-content: center;
    flex-direction : column;
}

.card-title-wrapper {
    height     : 80%;
    display    : flex;
    align-items: center;
}

.card-title-wrapper b {
    font-size: calc(2.4vmax + 12px);
}

.card-bottom-info {
    width          : calc(100% - 40px);
    display        : flex;
    justify-content: space-between;
    align-items    : center;
    padding        : 0 20px;
}

.fullscreen-icon {
    position: absolute;
    top: 0;
    left: calc(100% - 40px);
    cursor: pointer;
}