.textbox-wrapper {
    display: flex;
    align-items: center;
    flex-direction: column;
    overflow-y: scroll;
}

.textbox-wrapper * {
    margin: 20px 0;
}

.textbox-wrapper textarea {
    width: 80%;
    max-width: 400px;
    min-height: 150px;
}

textarea {
    font-family: inherit;
}

.textbox-wrapper textarea, input {
    padding: 5px 10px;
    border: 1px solid #ccc;
    border-radius: 6px;
}

.textbox-wrapper button {
    padding: 10px 30px;
    background-color: #35294F;
    color: white;
    border: none;
    border-radius: 16px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.textbox-wrapper button:hover, .textbox-wrapper button:active {
    box-shadow: none;
}

input:focus, textarea:focus {
    outline: none;
}