/* Basically just grid layout shit */

#root {
    height: 100%;
    width: 100%;

    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 10% 80% 10%;
}

/* wrapper element for searchbar */
.topbar-position-wrapper {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
}

.featured-card-wrapper {
    grid-row: 2 / 3;
    grid-column: 1 / 2;
}

.sidebar-icons-wrapper {
    grid-row: 3 / 4;
    grid-column: 1 / 2;
}