.admin-login-view {
    width : 100%;
    height: 100%;

    display        : flex;
    align-items    : center;
    justify-content: center;
    flex-direction : column;
    overflow-y     : scroll;
}

.admin-login-view input,
.admin-login-view button {
    width    : 65%;
    max-width: 300px;
    margin   : 5px;
}

.admin-login-view button {
    padding         : 5px 10px;
    background-color: #35294F;
    color           : white;
    border-radius   : 16px;
    border          : none;
    box-shadow      : 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
}

.admin-login-view button:hover {
    box-shadow: none;
}

.admin-card {
    border : 1px solid #ccc;
    margin : 20px;
    padding: 5px;
}

.admin-cards-wrapper {
    max-height: 100%;
}

.admin-card button.delete-button {
    background-color: rgb(228, 78, 78);
}