.sidebar-icons-wrapper {
    background-color: #15092F;
    display: flex;
    align-items: center;
    padding-left: 10vw;
}

.sidebar-icons-wrapper svg {
    margin: 0 20px;
    cursor: pointer;
}

.sidebar-icons-wrapper svg:active {
    transform: scale(1.2);
}

@media screen and (max-width: 500px) {
    .sidebar-icons-wrapper {
        padding-left: 0 !important;
        justify-content: space-around;
    }
}